// import axios from "../axios";
import axios, { get_login_token, get_user_data, LOGIN_TOKEN_KEY, set_login_token, set_user_data} from "../axios.js";

// import axios, {$axios, catch_handler, then_handler} from "../token";

export default {
    namespaced: true,
    state () {
        return {
            assignMentorList: {},
            assignMentor:{},
        }
    },
    mutations: {
        getMentorAssignes(state, assignMentorList)                
        {
            state.assignMentorList = assignMentorList;
        },
        assignMentor(state, assignMentor){
            state.assignMentor = assignMentor
        }
    },
    actions: {
        assignMentorList({commit}, param = {}) {
            axios().get('api/mentor-assigns', {
                params:  param
              })
            .then( response => {
                commit('getMentorAssignes', response.data);
                // console.log(response.data)
            });
        },
        assignMentor({commit}, param = {}){
            axios().get('/api/mentor-assigns/create', param)
            .then( response => {
                commit('assignMentor', response.data)
                console.log(response.data)
            })
        }


    }
}