import { data } from "autoprefixer";
import axios, { get_login_token, get_user_data, LOGIN_TOKEN_KEY, set_login_token, set_user_data, isLoggedIn} from "../../axios.js";

export default {
    namespaced: true,
    state () {
        return {
            user:  get_user_data() || { },
            isLoggedIn: get_login_token() ? true : false,
            errorMessage: '',
        }
    },
    getters:{
        authenticated( state ){
            return state.user && state.isLoggedIn 
        },
         user(state){
            return state.user
         }
    },
    mutations: {
        setLogin( state, data  ){
            if( data === false ) {
                state.user = false;
                state.isLoggedIn = false;
                set_login_token(false);
                set_user_data();
            } else {

                set_login_token( data.token );
                set_user_data( data.user  );

                // console.log('data',data.user)

                state.user = data.user
                state.isLoggedIn = true
            }
        },
        seterrorMessage(state, data){
            state.errorMessage = data;
        }
    },
    actions: {
       async findUser({commit}, param = {}) {

            try{
                const response = await axios().post( '/api/login', param.data );
                  if( response ) {                
                    return commit( "setLogin", response.data );
                  }
    
            } catch( r ){
                set_login_token(false);
                if( r.response ) {
                    commit( "seterrorMessage", r.response.data.message );
                    commit( "setLogin", null );       
                }
            }

            return response;
        },

        async logout( { commit }, params ){
            params = params || { };

            const afterThat = () => {
                commit( 'setLogin', false );
                if( typeof params.loggedOut == "function") {
                    params.loggedOut();
                }
            }

            return await axios( ).post( '/api/logout' )
                .then( () => { } )
                .catch( () => { } )
                .finally( afterThat );

        },
    },
    
}
